<template>
  <div>
    <img class="bg" src="@/assets/img/bg.jpg">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">
      <h1 class="h1"></h1>
                                                      <el-form-item v-if="tableName=='daoshixinxi'" label="" prop="daoshixinxidaoshigonghao">
        <el-input v-model="ruleForm.daoshigonghao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='daoshixinxi'" label="" prop="daoshixinximima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='daoshixinxi'" label="" prop="daoshixinxidaoshixingming">
        <el-input v-model="ruleForm.daoshixingming" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='daoshixinxi'" label="" prop="daoshixinxishoujihao">
        <el-input v-model="ruleForm.shoujihao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='daoshixinxi'" label="" prop="daoshixinxidaoshizhicheng">
        <el-input v-model="ruleForm.daoshizhicheng" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='daoshixinxi'" label="" prop="daoshixinxidianziyouxiang">
        <el-input v-model="ruleForm.dianziyouxiang" placeholder=""></el-input>
      </el-form-item>
                                                                  <el-form-item v-if="tableName=='kaoshengxinxi'" label="" prop="kaoshengxinxikaoshengzhanghao">
        <el-input v-model="ruleForm.kaoshengzhanghao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='kaoshengxinxi'" label="" prop="kaoshengxinximima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='kaoshengxinxi'" label="" prop="kaoshengxinxikaoshengxingming">
        <el-input v-model="ruleForm.kaoshengxingming" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='kaoshengxinxi'" label="" prop="kaoshengxinxinianling">
        <el-input v-model="ruleForm.nianling" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='kaoshengxinxi'" label="" prop="kaoshengxinxishoujihao">
        <el-input v-model="ruleForm.shoujihao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='kaoshengxinxi'" label="" prop="kaoshengxinxiyouxiang">
        <el-input v-model="ruleForm.youxiang" placeholder=""></el-input>
      </el-form-item>
                                                                                          <el-button @click="login()" class="btn-login" type="primary"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
              },
      tableName:"",
      rules: {}
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
  },
  methods: {
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    login() {
                                                      if((!this.ruleForm.daoshigonghao) && `daoshixinxi` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `daoshixinxi` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                              if(`daoshixinxi` == this.tableName && this.ruleForm.shoujihao&&(!this.$validate.isMobile(this.ruleForm.shoujihao))){
        this.$message.error(``);
        return
      }
                                                                                                                                                                  if(`daoshixinxi` == this.tableName && this.ruleForm.dianziyouxiang&&(!this.$validate.isEmail(this.ruleForm.dianziyouxiang))){
        this.$message.error(``);
        return
      }
                                                                                                            if((!this.ruleForm.kaoshengzhanghao) && `kaoshengxinxi` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `kaoshengxinxi` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                  if(`kaoshengxinxi` == this.tableName && this.ruleForm.nianling&&(!this.$validate.isIntNumer(this.ruleForm.nianling))){
        this.$message.error(``);
        return
      }
                                                                                                                        if(`kaoshengxinxi` == this.tableName && this.ruleForm.shoujihao&&(!this.$validate.isMobile(this.ruleForm.shoujihao))){
        this.$message.error(``);
        return
      }
                                                                  if(`kaoshengxinxi` == this.tableName && this.ruleForm.youxiang&&(!this.$validate.isEmail(this.ruleForm.youxiang))){
        this.$message.error(``);
        return
      }
                                                                                                      this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${this.tableName}/register`,
            method: "post",
            data:this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$router.replace({ path: "/login" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
