<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
    >  
     <el-row>
                                                      <el-col :span="12">
        <el-form-item  v-if="flag=='daoshixinxi'"  label="" prop="daoshigonghao">
          <el-input v-model="ruleForm.daoshigonghao" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='daoshixinxi'"  label="" prop="mima">
          <el-input v-model="ruleForm.mima" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='daoshixinxi'"  label="" prop="daoshixingming">
          <el-input v-model="ruleForm.daoshixingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="24">  
        <el-form-item v-if="flag=='daoshixinxi'" label="" prop="touxiang">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
          @change="daoshixinxitouxiangUploadChange"
          ></file-upload>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='daoshixinxi'"  label="" prop="shoujihao">
          <el-input v-model="ruleForm.shoujihao" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='daoshixinxi'"  label="" prop="daoshizhicheng">
          <el-input v-model="ruleForm.daoshizhicheng" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="flag=='daoshixinxi'"  label="" prop="suozaixuexiao">
          <el-select v-model="ruleForm.suozaixuexiao" placeholder="">
            <el-option
                v-for="(item,index) in daoshixinxisuozaixuexiaoOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='daoshixinxi'"  label="" prop="dianziyouxiang">
          <el-input v-model="ruleForm.dianziyouxiang" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                                                                  <el-col :span="12">
        <el-form-item  v-if="flag=='kaoshengxinxi'"  label="" prop="kaoshengzhanghao">
          <el-input v-model="ruleForm.kaoshengzhanghao" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='kaoshengxinxi'"  label="" prop="mima">
          <el-input v-model="ruleForm.mima" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='kaoshengxinxi'"  label="" prop="kaoshengxingming">
          <el-input v-model="ruleForm.kaoshengxingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="flag=='kaoshengxinxi'"  label="" prop="xingbie">
          <el-select v-model="ruleForm.xingbie" placeholder="">
            <el-option
                v-for="(item,index) in kaoshengxinxixingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='kaoshengxinxi'"  label="" prop="nianling">
          <el-input v-model="ruleForm.nianling" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="24">  
        <el-form-item v-if="flag=='kaoshengxinxi'" label="" prop="kaoshengtouxiang">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.kaoshengtouxiang?ruleForm.kaoshengtouxiang:''"
          @change="kaoshengxinxikaoshengtouxiangUploadChange"
          ></file-upload>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='kaoshengxinxi'"  label="" prop="shoujihao">
          <el-input v-model="ruleForm.shoujihao" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='kaoshengxinxi'"  label="" prop="youxiang">
          <el-input v-model="ruleForm.youxiang" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                                                                                          <el-form-item v-if="flag=='users'" label="" prop="username">
        <el-input v-model="ruleForm.username" 
        placeholder=""></el-input>
      </el-form-item>
      <el-col :span="24">
      <el-form-item>
        <el-button type="primary" @click="onUpdateHandler"> </el-button>
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
      ruleForm: {},
      flag: '',
      usersFlag: false,
                                                                                                                              daoshixinxisuozaixuexiaoOptions: [],
                                                                                                            kaoshengxinxixingbieOptions: [],
                                                                                                                                        };
  },
  mounted() {
    var table = this.$storage.get("sessionTable");
    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
                                                                                    this.$http({
      url: `option/xuexiaojieshao/xuexiaomingcheng`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.daoshixinxisuozaixuexiaoOptions = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
                                                                        this.kaoshengxinxixingbieOptions = ",".split(',')
                                                                                          },
  methods: {
                                                                                                                                                                                                                                                                                                daoshixinxitouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
                                                                                                                kaoshengxinxikaoshengtouxiangUploadChange(fileUrls) {
        this.ruleForm.kaoshengtouxiang = fileUrls;
    },
                                                                            onUpdateHandler() {
                                                      if((!this.ruleForm.daoshigonghao)&& 'daoshixinxi'==this.flag){
        this.$message.error('');
        return
      }
                                                            if((!this.ruleForm.mima)&& 'daoshixinxi'==this.flag){
        this.$message.error('');
        return
      }
                                                                                                                                                                              if( 'daoshixinxi' ==this.flag && this.ruleForm.shoujihao&&(!isMobile(this.ruleForm.shoujihao))){
        this.$message.error(``);
        return
      }
                                                                                                                                                                  if( 'daoshixinxi' ==this.flag && this.ruleForm.dianziyouxiang&&(!isEmail(this.ruleForm.dianziyouxiang))){
        this.$message.error(``);
        return
      }
                                                                                                            if((!this.ruleForm.kaoshengzhanghao)&& 'kaoshengxinxi'==this.flag){
        this.$message.error('');
        return
      }
                                                            if((!this.ruleForm.mima)&& 'kaoshengxinxi'==this.flag){
        this.$message.error('');
        return
      }
                                                                                                                                                                  if( 'kaoshengxinxi' ==this.flag && this.ruleForm.nianling&&(!isIntNumer(this.ruleForm.nianling))){
       this.$message.error(``);
        return
      }
                                                                                                                        if( 'kaoshengxinxi' ==this.flag && this.ruleForm.shoujihao&&(!isMobile(this.ruleForm.shoujihao))){
        this.$message.error(``);
        return
      }
                                                                  if( 'kaoshengxinxi' ==this.flag && this.ruleForm.youxiang&&(!isEmail(this.ruleForm.youxiang))){
        this.$message.error(``);
        return
      }
                                                                                                      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
